section ul {
  display: inline-block;
  text-align: left;
  list-style: disc; }
  section ul.no-list-style {
    list-style: none; }
  section ul > li {
    margin-bottom: 0.5em; }
    section ul > li ul {
      margin-top: 0.5em;
      display: block; }
    section ul > li ul > li {
      list-style-type: circle; }

.flexblock li a.bg-trans-light,
.flexblock li a.bg-red,
.flexblock li a.bg-green,
.flexblock li a.bg-blue,
